<template>
	
    <div class="main-page">
		
        <div v-if="show">
            <div class="b-info">
                <div class="b-info-background" :style="{ background: $config.theme }"></div>
                <div class="b-info-card shadow">
                    <!-- <van-image
            class="b-info-img shadowtwo"
            width="60"
            height="60"
            src=""
            round
          /> -->
                    <div style="
              background-color: rgb(204, 204, 204);
              width: 50px;
              position: relative;
              top: -23px;
              left: 50%;
              transform: translateX(-50%);
              border-radius: 50%;
            ">
                        <i class="iconfont icondefaultphoto" style="font-size: 50px; color: rgb(255, 255, 255)"></i>
                    </div>

                    <p style="line-height: 40px">{{ webMyInfo.Fname }}</p>
                    <p>{{ webMyInfo.OrgLongName }}</p>
                </div>
            </div>
            <div class="menu-list" v-for="(item, index) in menuList" :key="index" @click="toPage(item)">
                <div class="menu-list-l">
                    <van-icon :name="item.icon" :color="$config.theme" />
                    {{ item.name }}
                </div>
                <i class="iconfont icon-right" />
            </div>
            <van-button type="primary" block :color="$config.theme" style="width: calc(100% - 30px); margin: 15px" @click="loginOut">退 出</van-button>
        </div>
        <div v-else class="load">
            <van-loading type="spinner" />
        </div>
    </div>
</template>

<script>
export default {
    name: "My",
    props: {},
    components: {},
    data() {
        return {
            show: true,
            webMyInfo: {},
            menuList: [
                { name: "修改密码", icon: "setting", url: "/modular/oa/ChangePassword" },
                { name: "我的收藏", icon: "like", url: "/modular/oa/MyCollect" },
                { name: "我的点赞", icon: "star", url: "/modular/oa/MyPraise" }
            ],
        };
    },
    methods: {
        loginOut() {
            this.$u
                .confirm("请确认退出？")
                .then(() => {
                    this.$router.push("/Login");
                    localStorage.clear("up")
                })
                .catch(() => { });
        },
        toPage(item) {
            this.$router.push(item.url);
            this.show = false;
        },
    },
    mounted() {
        this.init();
        this.webMyInfo = JSON.parse(this.$u.getItem("myInfo"));
    },
};
</script>

<style lang="scss" scoped>
.main-page {
    .b-info {
        position: relative;
        height: 180px;

        .b-info-background {
            height: 150px;
        }

        .b-info-card {
            position: absolute;
            bottom: 0;
            left: 15px;
            width: calc(100% - 30px);
            height: 130px;
            text-align: center;
        }

        .b-info-img {
            margin-top: -30px;
        }
    }

    .menu-list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 50px;
        border-bottom: 1px solid #f2f2f4;
        padding: 0 15px;

        .iconfont {
            color: #000;
        }
    }
}
</style>